import React, { useState, useEffect } from "react";

/* ===========================
   CONSTANTS
=========================== */

const PRIMARY = "#553122";
const SECONDARY = "#b78831";
const TERTIARY = "#f7f0e1";

/* ===========================
   SAMPLE DATA
=========================== */

const menuItems = {
    breakfast: [
        { id: "B001", name: "Idli (4 pcs)", price: 60, image: "ü•ò", veg: true },
        { id: "B002", name: "Masala Dosa", price: 120, image: "ü•û", veg: true },
    ],
    lunch: [
        { id: "L001", name: "Andhra Meals", price: 250, image: "üç±", veg: true },
        { id: "L002", name: "Chicken Biryani", price: 280, image: "üçõ", nonveg: true },
    ],
};

const savedAddresses = [
    {
        id: 1,
        label: "üè† Home",
        address: "Banjara Hills, Hyderabad",
        eta: "30-35 mins",
        deliveryFee: 40,
    },
    {
        id: 2,
        label: "üè¢ Work",
        address: "HITEC City, Hyderabad",
        eta: "35-45 mins",
        deliveryFee: 60,
    },
];

/* ===========================
   COMPONENT
=========================== */

const Delivery = () => {
    const [page, setPage] = useState("location"); // location | menu | checkout | tracking
    const [selectedAddress, setSelectedAddress] = useState(null);
    const [cart, setCart] = useState([]);
    const [category, setCategory] = useState("breakfast");
    const [orderNumber, setOrderNumber] = useState(null);
    const [deliveryStatus, setDeliveryStatus] = useState("accepted");

    /* ===========================
       CART HELPERS
    =========================== */

    const addToCart = (item) => {
        if (!cart.find((c) => c.id === item.id)) {
            setCart([...cart, { ...item, quantity: 1 }]);
        }
    };

    const removeFromCart = (id) => {
        setCart(cart.filter((item) => item.id !== id));
    };

    const subtotal = cart.reduce(
        (total, item) => total + item.price * item.quantity,
        0
    );

    const deliveryFee =
        subtotal >= 499 ? 0 : selectedAddress?.deliveryFee || 0;

    const gst = subtotal * 0.05;
    const total = subtotal + deliveryFee + gst;

    /* ===========================
       ORDER PLACEMENT
    =========================== */

    const placeOrder = () => {
        const number =
            "ADL" + Math.floor(Math.random() * 10000).toString().padStart(4, "0");
        setOrderNumber(number);
        setPage("tracking");
    };

    /* ===========================
       DELIVERY STATUS SIMULATION
    =========================== */

    useEffect(() => {
        if (page === "tracking") {
            setTimeout(() => setDeliveryStatus("preparing"), 2000);
            setTimeout(() => setDeliveryStatus("packed"), 6000);
            setTimeout(() => setDeliveryStatus("out"), 10000);
            setTimeout(() => setDeliveryStatus("arriving"), 14000);
            setTimeout(() => setDeliveryStatus("delivered"), 18000);
        }
    }, [page]);

    /* ===========================
       RENDER
    =========================== */

    return (
        <div className="min-h-screen p-6 mt-[120px]" style={{ backgroundColor: TERTIARY }}>
            {/* ================= LOCATION PAGE ================= */}
            {page === "location" && (
                <>
                    <h2 className="text-3xl font-bold mb-6">
                        Where should we deliver?
                    </h2>

                    <div className="space-y-4">
                        {savedAddresses.map((addr) => (
                            <div
                                key={addr.id}
                                className="bg-white p-4 rounded shadow cursor-pointer hover:shadow-lg"
                                onClick={() => {
                                    setSelectedAddress(addr);
                                    setPage("menu");
                                }}
                            >
                                <h4 className="font-bold">{addr.label}</h4>
                                <p>{addr.address}</p>
                                <p className="text-sm text-gray-500">
                                    {addr.eta} ‚Ä¢ ‚Çπ{addr.deliveryFee} delivery
                                </p>
                            </div>
                        ))}
                    </div>
                </>
            )}

            {/* ================= MENU PAGE ================= */}
            {page === "menu" && (
                <>
                    <button onClick={() => setPage("location")} className="underline mb-4">
                        Back
                    </button>

                    <h2 className="text-3xl font-bold mb-4">Select Items</h2>

                    <div className="flex gap-4 mb-6">
                        {Object.keys(menuItems).map((cat) => (
                            <button
                                key={cat}
                                onClick={() => setCategory(cat)}
                                className={`px-4 py-2 rounded ${category === cat ? "text-white" : "border"
                                    }`}
                                style={{
                                    backgroundColor:
                                        category === cat ? PRIMARY : "white",
                                    borderColor: PRIMARY,
                                }}
                            >
                                {cat}
                            </button>
                        ))}
                    </div>

                    <div className="grid md:grid-cols-2 gap-6">
                        {menuItems[category].map((item) => (
                            <div key={item.id} className="bg-white p-4 rounded shadow">
                                <div className="text-3xl">{item.image}</div>
                                <h4 className="font-bold">{item.name}</h4>
                                <p className="font-bold">‚Çπ{item.price}</p>

                                {!cart.find((c) => c.id === item.id) ? (
                                    <button
                                        onClick={() => addToCart(item)}
                                        className="mt-2 px-4 py-2 text-white rounded"
                                        style={{ backgroundColor: PRIMARY }}
                                    >
                                        Add
                                    </button>
                                ) : (
                                    <button
                                        onClick={() => removeFromCart(item.id)}
                                        className="mt-2 px-4 py-2 bg-red-500 text-white rounded"
                                    >
                                        Remove
                                    </button>
                                )}
                            </div>
                        ))}
                    </div>

                    {cart.length > 0 && (
                        <div className="mt-6 p-4 bg-white rounded shadow">
                            <p>Subtotal: ‚Çπ{subtotal}</p>
                            <p>
                                Delivery:{" "}
                                {deliveryFee === 0 ? "FREE" : `‚Çπ${deliveryFee}`}
                            </p>
                            <p>GST: ‚Çπ{gst.toFixed(0)}</p>
                            <p className="font-bold">Total: ‚Çπ{total.toFixed(0)}</p>

                            <button
                                onClick={() => setPage("checkout")}
                                className="mt-4 px-6 py-3 text-white rounded"
                                style={{ backgroundColor: PRIMARY }}
                            >
                                Checkout
                            </button>
                        </div>
                    )}
                </>
            )}

            {/* ================= CHECKOUT PAGE ================= */}
            {page === "checkout" && (
                <>
                    <button onClick={() => setPage("menu")} className="underline mb-4">
                        Back
                    </button>

                    <h2 className="text-3xl font-bold mb-6">Review Order</h2>

                    <div className="bg-white p-4 rounded shadow mb-6">
                        {cart.map((item) => (
                            <div key={item.id} className="flex justify-between mb-2">
                                <span>
                                    {item.name} √ó {item.quantity}
                                </span>
                                <span>‚Çπ{item.price}</span>
                            </div>
                        ))}
                        <hr className="my-2" />
                        <p>Total: ‚Çπ{total.toFixed(0)}</p>
                    </div>

                    <button
                        onClick={placeOrder}
                        className="w-full py-4 text-white rounded"
                        style={{ backgroundColor: PRIMARY }}
                    >
                        Place Order ‚Ä¢ ‚Çπ{total.toFixed(0)}
                    </button>
                </>
            )}

            {/* ================= TRACKING PAGE ================= */}
            {page === "tracking" && (
                <>
                    <h2 className="text-3xl font-bold text-green-600 mb-4">
                        Order Placed!
                    </h2>

                    <p className="mb-4">Order #{orderNumber}</p>

                    <div className="bg-white p-4 rounded shadow mb-6">
                        <p>
                            {deliveryStatus === "accepted" && "‚úÖ Order Accepted"}
                            {deliveryStatus === "preparing" && "üî• Preparing"}
                            {deliveryStatus === "packed" && "üì¶ Packed"}
                            {deliveryStatus === "out" && "üõµ Out for Delivery"}
                            {deliveryStatus === "arriving" && "üìç Arriving Soon"}
                            {deliveryStatus === "delivered" && "üéâ Delivered"}
                        </p>
                    </div>

                    <button
                        onClick={() => window.location.reload()}
                        className="px-6 py-3 text-white rounded"
                        style={{ backgroundColor: PRIMARY }}
                    >
                        Order Again
                    </button>
                </>
            )}
        </div>
    );
};

export default Delivery;